import * as components from './vendor/vue';
import ComponentWrapper from './vendor/vue/util/ComponentWrapper';

const __TACHYON_VERSION__ = '1.0.0';

const install = (function () {
    let installed = false;
    return function (Vue, components) {
        if (installed) {
            return;
        }
        if (components) {
            for (let key in components) {
                Vue.component(key, components[key]);
            }
            installed = true;
        }
    }
})();

const TachyonVue = {
    'version': __TACHYON_VERSION__,
    'install': (Vue, options) => {
        install(Vue, components)
    },
};

const Vue = window && window.Vue;
if (Vue) {
    Vue.use(TachyonVue);
}

export {TachyonColumn, TachyonGrid, TachyonTreeColumn, TachyonTreeGrid} from './vendor/vue';
export {ComponentWrapper};
export default TachyonVue;
