import GridCore from './mixins/GridCore';
import {unobserve} from "./util/unobserve";

export default {
    name: 'tachyon-grid',
    mixins: [GridCore],
    mounted() {
        let instance = this.gridInstance;
        if (!instance) {
            instance = this.gridInstance = unobserve(tachyon.createGrid(this.$el, {}));
        }

        const node = this.$refs.columns;
        if (node && node.parentElement) {
            node.parentElement.removeChild(node);
        }

        this.gridInstance.columns = this.$children.map(child => child.columnInstance);
    },
    methods: {}
}