import Vue from 'vue';

export default Vue.extend({
    name: 'tachyon-tree-item-renderer',
    inject: {
        grid: {
            default: null,
        },
    },
    props: {
        indent: {
            type: Number,
            default: 30,
        },
    },
    data() {
        return {
            state: () => ({}),
            hasChildren: false,
            level: -1,
            isOpened: false,
        };
    },
    computed: {
        item() {
            return this.state.item;
        },
        column() {
            return this.state.column;
        },
        label() {
            return this.state.label;
        },
        staticClass() {
            return 'tachyon-tree__item';
        },
        attrs() {
            return {
                selected: this.state.selected,
                hovered: this.state.hovered,
            };
        },
        scopedProps() {
            return {
                item: this.state.item,
                cell: this.state.cell,
                cellPosition: this.state.cellPosition,
                column: this.state.column,
                label: this.label,
                level: this.level,
                isOpened: this.isOpened,
                hasChildren: this.hasChildren,
                state: this.state,
            };
        },
        calculatedLeft() {
            if (this.column && this.column.boxMode) {
                return 0;
            }
            return (this.level || 0) * this.indent + 'px';
        },
    },
    methods: {
        genLabel() {
            if (!this.item) {
                return null;
            }
            const children = [];
            if (this.$scopedSlots.itemRenderer) {
                children.push(this.$scopedSlots.itemRenderer(this.scopedProps));
            } else {
                children.push(this.label);
            }

            return children;
        },
        genContent() {
            return [
                this.genToggle(),
                this.genLabel(),
            ];
        },
        genToggle() {
            const children = [];
            if (this.$scopedSlots.toggle) {
                children.push(this.$scopedSlots.toggle(this.scopedProps));
            } else {
                children.push(this.isOpened ? '-' : '+');
            }

            return this.$createElement('div', {
                staticClass: this.staticClass + '__toggle',
                class: {
                    'toggle--open': this.isOpened,
                },
                style: {
                    visibility: this.hasChildren ? '' : 'hidden',
                },
                on: {
                    mousedown: (event) => {
                        event.stopPropagation();
                        this.grid.toggleNode(this.item);
                    },
                },
            }, children);
        },
    },

    render(h) {

        const style = {};

        return h('div', {
            staticClass: this.staticClass,
            class: {
                [this.staticClass + '--leaf']: !this.hasChildren,
            },
            style: {
                paddingLeft: this.calculatedLeft,
            },
            attrs: this.attrs,
        }, this.genContent());
    },

    /**
     * Tachyon Life Cycle
     * 렌더러 갱신시 호출됩니다.
     * @param grid
     * @param state
     */
    prepare(grid, state) {
        const item = state.item;
        this.hasChildren = grid.hasChildren(item);
        this.level = grid.getNodeLevel(item);
        this.isOpened = grid.isOpenNode(item);
    },
});
