import templateToFactory from '../util/templateToFactory';

const GridProperties = [
    'items', 'columns', 'labelFunction', 'rowHeight', 'headerRowHeight', 'autoRowHeight', 'rowCount', 'maxRowCount',
    'useNullItemRenderer', 'autoMerge',
    'frozenLeft', 'frozenRight', 'frozenTop', 'frozenBottom',
    'mergeCompare', 'sortableColumns', 'resizableColumns', 'draggableColumns', 'editable', 'editOnKeys', 'editOnEvents',
    'selectionMode', 'theme', 'styles', 'pasteFromClipboard', 'selectOnDrag'
];

const GridMethods = [
    'itemToLabel', 'scrollToCell', 'focus', 'getSortColumns', 'sortByColumns',
    'getAddon', 'addAddon',
    'addColumn', 'addColumnAt', 'addColumns', 'addColumnsAt', 'removeColumn', 'removeColumnsAt',
    'getMergeCells', 'addMergeCells', 'addMergeCell', 'removeMergeCell', 'removeMergeCells',
    'invalidate', 'validateNow', 'lockDisplay', 'unlockDisplay',
    'isCellEditable', 'startEdit', 'endEdit', 'cancelEdit', 'isEditing', 'analysisMergeAll'
];

const GridEvents = [
    'selection-changing', 'selection-change',
    'collection-change', 'node-change', 'column-collection-change', 'column-node-change', 'scroll', 'normalized-columns-change',
    'item-over', 'item-out', 'item-down', 'item-up', 'item-drag', 'item-click', 'item-double-click',
    'item-edit-starting', 'item-edit-start', 'item-edit-ending', 'item-edit-end', 'item-edit-cancel',
    'header-double-click', 'header-down', 'header-up', 'header-click', 'header-drag', 'header-drop', 'header-out', 'header-over',
    'selection-drag-start', 'selection-drag-end'
];

const defaultProps = {};
const defaultWatch = {};
const defaultMethods = {};

function setupProp(name) {
    defaultProps[name] = undefined;
    defaultWatch[name] = function (newVal, oldVal) {
        if (this.gridInstance) {
            this.gridInstance[name] = newVal;
        }
    };
}

GridProperties.forEach(name => {
    setupProp(name);
});

GridMethods.forEach(name => {
    defaultMethods[name] = function (...args) {
        if (this.gridInstance) {
            return this.gridInstance[name].apply(this.gridInstance, args);
        }
    };
});

export default {
    name: 'grid-core',
    provide() {
        return {grid: this}
    },
    props: {
        ...defaultProps,
        //'headerRenderer', 'itemRenderer', 'mergeItemRenderer', 'nullItemRenderer',
        headerRenderer: {
            type: [Function, Object],
        },
        itemRenderer: {
            type: [Function, Object],
        },
        mergeItemRenderer: {
            type: [Function, Object],
        },
        nullItemRenderer: {
            type: [Function, Object],
        },
        itemEditor: {
            type: [Function, Object]
        }
    },
    data() {
        return {
            gridInstance: null,
            selectedItems: () => ([]),
            selectedItem: null,
            selectedIndices: () => ([]),
            selectedIndex: -1,
            selectedCells: () => ([]),
            selectedCell: null,
            pendingValue: this.value,
            deferredFunc: null
        }
    },
    computed: {
        collection() {
            return this.items && this.gridInstance && this.gridInstance.collection || null;
        },
        staticClass() {
            return `${this.$options.name}-wrapper`;
        },
        normalizedColumns() {
            if (this.items && this.columns) {
            }
            return this.gridInstance && this.gridInstance.normalizedColumns || [];
        }
    },
    watch: {
        ...defaultWatch,
        gridInstance(grid) {
            if (grid) {
                GridEvents.forEach((type) => {
                    grid.addEventListener(type, this.onGridInstanceEvents);
                });

                const rendererProps = ['headerRenderer', 'itemRenderer', 'mergeItemRenderer', 'nullItemRenderer', 'itemEditor'];
                const props = this.$props;
                for (let p in props) {
                    let value = props[p];
                    if (value !== undefined) {
                        if (rendererProps.indexOf(p) >= 0) {
                            grid[p] = templateToFactory(this, value);
                        } else {
                            grid[p] = value;
                        }
                    }
                }

                if (this.deferredFunc) {
                    this.deferredFunc();
                    this.deferredFunc = null;
                }

                grid.hookFreeElement = (element) => {
                    if (element.htmlElement && element.htmlElement.__vue__) {
                        element.htmlElement.__vue__.$destroy();
                    }
                    return false;
                }
            }
        },
        headerRenderer(value) {
            this.gridInstance.headerRenderer = templateToFactory(this, value);
        },
        itemRenderer(value) {
            this.gridInstance.itemRenderer = templateToFactory(this, value);
        },
        mergeItemRenderer(value) {
            this.gridInstance.mergeItemRenderer = templateToFactory(this, value);
        },
        nullItemRenderer(value) {
            this.gridInstance.nullItemRenderer = templateToFactory(this, value);
        },
        itemEditor(value) {
            this.gridInstance.itemEditor = templateToFactory(this, value);
        },
        value: {
            handler(value) {
                if (value && !Array.isArray(value)) {
                    value = [value];
                }
                this.setSelections(value);
            }
        },
        selectedItems(value) {
            this.setSelections(value);
        },
        selectedItem(value) {
            this.setSelections(value);
        },
        selectedIndices(value) {
            this.setSelections((value || []).map(i => this.items[i]));
        },
        selectedIndex(value) {
            this.setSelections(this.items[value]);
        },
        selectedCells(value) {
            this.setSelections(value);
        },
        selectedCell(value) {
            this.setSelections(value);
        },
    },
    beforeDestroy() {
        if (this.gridInstance) {
            GridEvents.forEach((type) => {
                this.gridInstance.removeEventListener(type, this.onGridInstanceEvents);
            });
            this.gridInstance.destroy();
        }
    },
    methods: {
        ...defaultMethods,
        genColumns() {
            const slots = this.$slots.default || [];
            const found = slots.find((vnode) => vnode.tag === 'columns');
            const children = found ? found.children : [];
            return this.$createElement('template', {
                ref: 'columns'
            }, children);
        },
        setSelections(value) {
            if (this.lockSelect) {
                return;
            }

            if (!this.gridInstance) {
                this.deferredFunc = () => {
                    this.setSelections(value);
                };
                return;
            }

            if (value != null) {
                value = Array.isArray(value) ? value : [value];
            }

            if (!this.gridInstance.isCellMode) {
                this.gridInstance.selectedItems = value;
            } else {
                this.gridInstance.selectedCells = value;
            }
            this.pendingValue = null;
        },
        syncSelection() {
            const grid = this.gridInstance;
            const selector = grid.selector;

            this.lockSelect = true;

            this.selectedItems = grid.selectedItems;
            this.selectedItem = grid.selectedItem;
            this.selectedIndices = grid.selectedIndices;
            this.selectedIndex = grid.selectedIndex;
            this.selectedCells = grid.selectedCells;
            this.selectedCell = grid.selectedCell;

            let result;
            if (selector.isRowMode) {
                result = selector.isMultipleMode ? grid.selectedItems : grid.selectedItem;
            } else {
                result = selector.isMultipleMode ? grid.selectedCells : grid.selectedCell;
            }

            this.$emit('input', result);
            this.$nextTick(() => {
                this.lockSelect = false;
            });
        },
        onGridInstanceEvents(event) {
            const {type, detail} = event;
            if (type === 'selection-change') {
                this.syncSelection();
            } else if (type === 'collection-change' && detail.kind === 'reset') {
                if (this.pendingValue) {
                    this.setSelections(this.pendingValue);
                }
            } else if (type === 'column-collection-change') {
            }
            this.$emit(type, event);
        }
    },
    render: function render(h) {
        const children = [this.genColumns()];
        return h('div', {
            staticClass: this.staticClass
        }, children);
    }
}
