import ColumnCore, {createWatch} from './mixins/ColumnCore';
import TachyonTreeItemRenderer from './TachyonTreeItemRenderer';
import templateToFactory from './util/templateToFactory';
import {unobserve} from "./util/unobserve";

export default {
    name: 'tachyon-tree-column',
    mixins: [ColumnCore],
    props: {
        indent: {
            type: Number,
            default: 20
        },
        /**
         * 노드 모양을 '┌' 보이게 합니다.
         * @returns {Boolean}
         */
        boxMode: {
            type: Boolean,
            default: false
        },
        headerRenderer: {
            type: [Function, Object],
            default: null,
        },
        itemRenderer: {
            type: [Function, Object],
            default: TachyonTreeItemRenderer,
        }
    },
    computed: {
        columnInstance() {
            return unobserve(tachyon.createTreeColumn());
        }
    },
    watch: {
        indent: createWatch('indent'),
        boxMode: createWatch('boxMode'),
        headerRenderer: {
            immediate: true,
            handler(value) {
                if (value) {
                    this.columnInstance.headerRenderer = templateToFactory(this, value);
                }
            }
        },
        itemRenderer: {
            immediate: true,
            handler(value) {
                if (value) {
                    this.columnInstance.itemRenderer = templateToFactory(this, this.itemRenderer);
                }
            }
        },
    },
}
