import Vue from 'vue';
import ComponentWrapper from './ComponentWrapper';

const vueComponentProps = ['render', 'name'];

function isVueExtendComponent(component) {
    return component instanceof Function && component['super'] === Vue;
}

function buildFactory(parent, template) {
    let component = null;
    if (isVueExtendComponent(template)) {
        component = template;
    } else if (template && typeof template === 'object' && vueComponentProps.every(p => p in template)) {
        component = Vue.extend(template);
    }

    if (component) {
        return Object.freeze(ComponentWrapper.factory(parent, component));
    }

    return template;
}

export default function templateToFactory(parent, template) {
    let factory = null;
    if (!isVueExtendComponent(template) && template instanceof Function) {
        factory = function () {
            return buildFactory(parent, template.apply(parent, arguments));
        }
    } else {
        factory = buildFactory(parent, template);
    }

    return factory || template;
}
