function fnCall(func, params, target) {
    if (func instanceof Function) {
        return func.apply(target, params)
    }
}

export default class ComponentWrapper {
    constructor(parent, wrapper) {
        this.parent = parent;
        this.wrapper = wrapper;
    }

    create(options = {}) {
        const component = new this.wrapper({
            parent: this.parent,
            ...options
        });
        component.$scopedSlots = this.parent.$scopedSlots;
        component.$mount();
        return component;
    }

    static factory(parent, wrapper, options = {}) {
        const factory = new ComponentWrapper(parent, wrapper);
        return {
            get htmlElement() {
                return this.compoenent.$el;
            },
            created(grid) {
                this.compoenent = factory.create({grid, ...options});
            },
            prepare(grid, state) {
                if (this.compoenent.state) {
                    this.compoenent.state = state;
                }
                fnCall(this.compoenent.$options.prepare, arguments, this.compoenent);
            },
            paint(ctx, state, w, h) {
                fnCall(this.compoenent.$options.paint, arguments, this.compoenent);
            },
            dispose() {

            },
            /**
             * ItemEditor 인터페이스
             */
            getSaveValue() {
                return fnCall(this.compoenent.$options.getSaveValue, arguments, this.compoenent);
            },
        }
    }
}
