import Vue from 'vue';

const Observer = new Vue.observable({}).__ob__.constructor;
const dummyObserver = new Observer({DUMMY: 'DUMMY'});

export function unobserve(value) {
    Object.defineProperty(value, '__ob__', {
        value: dummyObserver,
        enumerable: false,
        configurable: true,
    });

    return value;
}