const ColumnProps = [
    'dataField', 'headerText', 'width', 'minWidth', 'visible', 'styles', 'id',
    'editable', 'editOnEvents', 'resizable', 'labelFunction',
    'sortable', 'sortCompare', 'sortDescending', 'isSortOriginal', 'calculateHeaderLayout',
    'isRowMerge', 'isColumnMerge', 'mergeDependColumns', 'open', 'format', 'filter'
];

const defaultProps = {};
const defaultWatch = {};

function setupProp(name) {
    defaultProps[name] = undefined;
    defaultWatch[name] = createWatch(name);
}

export function createWatch(name) {
    return {
        immediate: true,
        handler: function (value) {
            if (value !== undefined) {
                this.columnInstance[name] = value;
            }
        }
    }
}

ColumnProps.forEach(p => setupProp(p));

export default {
    name: 'column-core',
    props: defaultProps,
    computed: {
        columnInstance() {
            throw new Error('Not implemented !!!');
        }
    },
    watch: defaultWatch,
    mounted() {
        this.columnInstance.children = this.$children.map(child => child.columnInstance);
    },
    render(h) {
        return h('template', this.$slots.default);
    }
}
